import { dbService } from "fbase";
import React, { useEffect, useRef,useState } from "react";
import CanvasDraw from "../../src/canvasDrawIndex";

const CCsubmit =({CsubmitObj, isOwner,viewDeleyToggle})=>{
    const [editing,setEditing]= useState(false);
    const [newCsubmit,setnewCsubmit]=useState(CsubmitObj.text);
    // const [givedHeart,setGivedHeart]=useState(false)
    // const [yourheart,setYourHeart]=useState(CsubmitObj.heart);
    const canvasRef2 = useRef();
    const [onOff,setOnOff]=useState(true);
    // const [onReview,setOnReview]= useState(false);
    // const [offReview,setoffReview]= useState(false);
    // // canvasRef2.current.loadSaveData(CsubmitObj.text);
    let uploadDay= new Date(CsubmitObj.createdAt);
    const [viewRefPin,setViewRefpin]=useState(false);
    // useEffect(() => {
    //    
    //     //  if(viewsToggle && onOff){
    //     //     canvasRef2.current.loadSaveData(CsubmitObj.text,true);
    //     //     setOnOff(false)
    //     //  }
        
    // },[editing]);
    const onDeleteClick= () => {
        const ok = window.confirm("wanna delete this?")
        if(ok){
            dbService.doc(`Csubmit/${CsubmitObj.id}`).delete();
            
        }
    };
    const toggleReview=()=> {
        canvasRef2.current.clear();
        // canvasRef2.current.loadSaveData(CsubmitObj.text);
        canvasRef2.current.loadSaveData(CsubmitObj.text);
        
    };
    const offReview=()=>{
        setEditing(false);
            //  
            // console.log('none')
            // const dataUrl=canvasRef2.current.getDataURL();
        
    
        
            // dbService.doc(`Csubmit/${CsubmitObj.id}`).update({
            
            // dataUrl:dataUrl,
           //  refPhoto:{refPhoto},
            //pinNumber:randomPinNum,
        // });
        
    }
    const onReview=()=> {setEditing(true);
        setTimeout(function() {
            canvasRef2.current.loadSaveData(CsubmitObj.text);
            
            
          }, 5);
        }
    const onSubmit = (event)=> {
        event.preventDefault();
        dbService.doc(`Csubmit/${CsubmitObj.id}`).update({
            text:newCsubmit,
        });
        setEditing(false);

    }
    const onChange = (event)=> {
        const {target:{value},
        }=event;
        setnewCsubmit(value);
    }
    //const toggleheart=()=> setYourHeart(prev => !prev);
    const onHeartClick= async() =>{
        setOnOff(prev => !prev);
        await dbService.doc(`Csubmit/${CsubmitObj.id}`).update({
           heart:(1+(CsubmitObj.heart)),
        })
        console.log(CsubmitObj.heart)

    }
    return(
        <div   style={{float:'left', border: '10px solid rgba(0, 0, 0, 0.05)',width:"420px"
        }}>
            
        {
            editing ? (
            <>
            {/* <form onSubmit={onSubmit}><input value={newCsubmit} required onChange={onChange}/>
            <input type={"submit"} value="Update croquis"/>
            </form> */}
            
            <div><CanvasDraw 
            style={{ touchAction: 'disabled'  ? 'auto' : 'none' }}
            ref={canvasRef2}
             disabled
            hideInterface
            
             loadTimeOffset={5}
             /></div>
             
             <button onClick={toggleReview}>Rewind</button>
             <button onClick={offReview}>Cancel</button>
             
            </>
            ):(
            <>
            <div style={{margin: '0 auto'}}><img src={CsubmitObj.dataUrl} height="400px" ></img></div>
            
            
        {isOwner ? (
            <>

            {/* <button disabled> {CsubmitObj.heart}</button> */}
            <button onClick={onHeartClick}>{CsubmitObj.heart}</button>
            <button onClick={onDeleteClick}>Delete</button>
            <button onClick={onReview}>Review</button>
            
            
            </>
        ):(
            <>
            <button onClick={onHeartClick}> {CsubmitObj.heart}</button>
            
            </>
        )}

            
            
                
            
            
            
            
        </>
            
            
            
            

            )}
            <button onClick={()=>{setViewRefpin(pre=>!pre)}}>Ref Pin</button>
            {"   "}  {uploadDay.toLocaleString(navigator.language || navigator.userLanguage)}
            {viewRefPin ? (<>
                <div><iframe  src={"https://assets.pinterest.com/ext/embed.html?id="+CsubmitObj.refPinUrl} height="600" width="300" frameBorder={0} scrolling="no" ></iframe>
                </div></>):(<></>)}
    </div>
)
};
    

export default CCsubmit;