import CCsubmit from "components/Csubmit";
import { authService } from "fbase";
import React, { useEffect, useState } from "react";
import { dbService } from "fbase";
export default ({userObj}) => {
    const onLogOutClick = () => authService.signOut();
    
    const [Csubmits,setCsubmits]= useState([]);
    const [viewToggle,setViewToggle]=useState(false);
    const [viewDeleyToggle,setViewDeleyToggle]=useState(false);
    // const getMyCroquis = async()=>{
    //     const croquisPost = await dbService.collection("Csubmit")
    //     .where("creatorId","==",userObj.uid)
    //     .orderBy("createdAt","desc")
    //     .get();
    //     console.log(croquisPost.docs.map((doc)=>doc.data()));
    //     setCsubmits(croquisPost.docs.map((doc)=>doc.data()));
    // }

    // useEffect(()=>{
    //     getMyCroquis();
    // },[]);
    useEffect(()=> { 
    dbService.collection("Csubmit")
        .where("creatorId","==",userObj.uid)
        .orderBy("createdAt","desc")
        .onSnapshot((snapshot) =>{
        const CsubmitArray = snapshot.docs.map((doc)=>({
            id: doc.id,
            ...doc.data(),
        }));
        
        setCsubmits(CsubmitArray);
        //console.log(Csubmits);
    });


    },[]);
    return (
    <>
        
        <span>Profile</span>
        <button onClick={onLogOutClick}>Log Out</button>
        <div>
          {Csubmits.map((Csubmit)=>(
              
              <CCsubmit 
            //   key={Csubmit.id} 
              CsubmitObj={Csubmit} isOwner={Csubmit.creatorId=== userObj.uid} viewsToggle={viewDeleyToggle}  />
              
          ))}
      </div>
        {/* {viewToggle ?(<>
            <button onClick={(e)=>{setViewToggle(false)}}>
          Close Drawings</button>
          
      </>):(<>
        <button onClick={(e)=>{setViewToggle(true)}}>
        
          Show Drawings</button>
      
        </>)} */}
        
       
            {/* <button onClick={(e)=>{setViewToggle(true)}}>
          Close Drawings</button>
          <div>
          {Csubmits.map((Csubmit)=>(
              
              <CCsubmit 
            //   key={Csubmit.id} 
              CsubmitObj={Csubmit} isOwner={Csubmit.creatorId=== userObj.uid} viewsToggle={viewToggle}  />
              
          ))}
      </div> */}
         
        
    </>
    
    )
};