import React from "react";
import { Link } from "react-router-dom";
const Navigation =() => (
<nav> 
    <ul>
        <li style={{display:'inline', borderLeft:'1px solid #999',padding:'0 10px'}}>
            <Link to ="/">Home</Link>
        </li>
        
        <li style={{display:'inline', borderLeft:'1px solid #999',padding:'0 10px'}}>
            <Link to ="/profile">My Drawings</Link>
        </li>

        </ul>
</nav>
);
export default Navigation;